














import Vue from 'vue'
import { mapState, mapActions } from 'vuex'

export default Vue.extend({
  name: 'App',
  watch: {
    $route: function(to, from) {
      if (to.path !== from.path) {
        this.nextPage = null
      }
    },
  },
  data() {
    return {
      nextPage: null as null | string,
    }
  },
  computed: {
    ...mapState(['isLoad']),
  },
  methods: {
    ...mapActions(['load']),
    next() {
      if (this.nextPage) {
        this.$router.push(this.nextPage)
      }
    },
  },
  async mounted() {
    if (!this.isLoad) {
      await this.load()
    }
  },
})
