
































import Vue from 'vue'
import { mapActions, mapState } from 'vuex'
import backBtn from './parts/btn/back.vue'

export default Vue.extend({
  name: 'setting2',
  components: {
    backBtn,
  },
  computed: {
    ...mapState(['gameSetting']),
  },
  watch: {
    gameSetting() {
      this.saveSetting(this.gameSetting)
    },
  },
  mounted() {
    this.$emit('next', '/setting-3')
  },
  methods: {
    ...mapActions(['saveSetting']),
  },
})
